*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  vertical-align: top;
  max-width: 100%;
}

body {
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: "Seravek";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local(""), url("../font/Seravek-Bold.woff2") format("woff2"), url("../font/Seravek-Bold.woff") format("woff");
}
@font-face {
  font-family: "Sfuitext";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(""), url("../font/SFUIText-Regular.woff2") format("woff2"), url("../font/SFUIText-Regular.woff") format("woff");
}
.some-back {
  background-image: url("../img/01.jpg");
}

.font1 {
  font-family: "Seravek";
  font-weight: 700;
}

.font2 {
  font-family: "Sfuitext";
}

.section {
  background-image: image-set("../img/02.webp" 1x, "../img/02@2x.webp" 2x);
}

.section-2 {
  min-height: 400px;
  background-image: url("../img/02.jpg");
  background-image: image-set(url("../img/02.webp") type("image/webp") 1x, url("../img/02@2x.webp") type("image/webp") 2x, url("../img/02.jpg") 1x, url("../img/02@2x.jpg") 2x);
}

.header {
  background-color: crimson;
  padding: 3em;
}

